<template>
    <p-data-table
        :value="testResults"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm"
        :loading="loading"
        v-model:selection="selectedBloodTest"
        selectionMode="single"
        dataKey="testOrderId"
        :scrollable="true"
        scrollHeight="calc(100vh - 130px)"
    >
        <p-column header="ID" field="testOrderId" />
        <p-column header="Order Date" field="bloodTestOrderDate" />
        <p-column header="E2" field="etwo">
            <template #body="{data}">
                {{
                    data.etwo === null || data.etwo === ""
                        ? data.etwoStat
                        : data.etwo
                }}
            </template>
        </p-column>
        <p-column header="LH" field="lh">
            <template #body="{data}">
                <span v-if="data.lh === null || data.lh == ''">
                    {{ data.lhStat }}
                </span>
                <span
                    v-else
                    :style="isStyleRed('LH', data.lh) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.lh) }"
                    >{{ data.lh }}
                </span>
            </template>
        </p-column>
        <p-column header="PROG" field="prog">
            <template #body="{data}">
                <span v-if="data.prog === null || data.prog == ''">
                    {{ data.progStat }}
                </span>
                <span
                    v-else
                    :style="
                        isStyleRed('PROG', data.prog) ? { color: 'red' } : {}
                    "
                    :class="{ number: !isNaN(data.prog) }"
                    >{{ data.prog }}
                </span>
            </template>
        </p-column>
        <p-column header="FSH" field="fsh">
            <template #body="{data}">
                <span v-if="data.fsh === null || data.fsh == ''">
                    {{ data.fshStat }}
                </span>
                <span
                    v-else
                    :style="isStyleRed('FSH', data.fsh) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.fsh) }"
                    >{{ data.fsh }}
                </span>
            </template>
        </p-column>
        <p-column header="TSH" field="tsh">
            <template #body="{data}">
                <span v-if="data.tsh === null || data.tshStat == ''">
                    {{ data.tshStat }}
                </span>
                <span
                    v-else
                    :style="isStyleRed('TSH', data.tsh) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.tsh) }"
                    >{{ data.tsh }}
                </span>
            </template>
        </p-column>
        <p-column header="PRL" field="prl">
            <template #body="{data}">
                <span v-if="data.prl === null || data.prl == ''">
                    {{ data.prlStat }}
                </span>
                <span
                    v-else
                    :style="isStyleRed('PRL', data.prl) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.prl) }"
                    >{{ data.prl }}
                </span>
            </template>
        </p-column>
        <p-column header="BhCG" field="bhcg">
            <template #body="{data}">
                {{
                    data.bhcg === null || data.bhcg === ""
                        ? data.bhcgStat
                        : data.bhcg
                }}
            </template>
        </p-column>
        <p-column header="DHEAS" field="dheas">
            <template #body="{data}">
                {{
                    data.dheas === null || data.dheas === ""
                        ? data.dheasStat
                        : data.dheas
                }}
            </template>
        </p-column>
        <p-column header="AMHP" field="amh">
            <template #body="{data}">
                {{
                    data.amh === null || data.amh === ""
                        ? data.amhStat
                        : data.amh
                }}
            </template>
        </p-column>
        <p-column header="" sty>
            <template #body="{data}">
                <span class="p-buttonset">
                    <p-button
                        label="Barcode"
                        class="p-button-sm"
                        @click="showBarcodeV2(data)"
                    />
                    <p-button
                        class="p-button-sm"
                        icon="pi pi-print"
                        @click="printToZebraPrinter(data)"
                    />
                </span>
            </template>
        </p-column>

        <template #footer>
            <p-button
                label="Order Test"
                class="p-mr-2"
                @click="add"
                v-if="hasCreatePermission"
            />
            <p-button
                label="Edit"
                class="p-mr-2"
                @click="edit"
                v-if="hasUpdatePermission"
            />
            <p-button label="Report" class="p-mr-2" @click="openPdfReport" />
            <p-button
                label="Add Blood Test"
                class="p-mr-2"
                @click="addBlood"
                v-if="hasCreatePermission"
            />
        </template>
    </p-data-table>
</template>

<script>
import { useBloodTestsReportInit } from "@/composable/blood-tests";
import { useDailyReportReportsInit } from "@/composable/daily-report";
import { inject, onMounted, onUnmounted, ref } from "vue";
import { useToast } from "primevue/usetoast";
import { emitter } from "@/event-bus";
import { usePermissionCheck } from "@/composable/users";
import { ZEBRA_PRING_URL } from "@/constants";
import { $axios } from "@/service/app-http";

export default {
    setup() {
        const {
            store,
            testResults,
            loading,
            getBloodTestResults,
            getBloodTestResultsWithOpenBarcode,
            orderBloodTestConfirmDialog,
            editDailogVisible,
            addBloodDialogvisible,
            selectedBloodTest,
            showBarcode,
            showBarcodeV2,
            openPdfReport
        } = useBloodTestsReportInit();

        const { isStyleRed } = useDailyReportReportsInit();

        const checkScannedBarcode = async (_barcode) => {
            if (testResults.value) {
                const isBarcodeMatch = testResults.value.some(
                    (row) => row.testOrderId === _barcode
                );

                // Output the result
                if (isBarcodeMatch) {
                    emitter.emit("cycles-ui:play-beep", 1);
                    // Example: Use toast to display the value
                    toast.add({
                        severity: "success",
                        summary: _barcode,
                        detail: "Barcode is correct. Confirmed!",
                        life: 3000
                    });
                } else {
                    if (
                        store.state.artCycle.patient &&
                        store.state.artCycle.patient.chart == _barcode
                    ) {
                        emitter.emit("cycles-ui:play-beep", 1);
                        toast.add({
                            severity: "warn",
                            summary: _barcode,
                            detail: "Patient is correct. Confirmed!",
                            life: 3000
                        });

                        return;
                    }

                    emitter.emit("cycles-ui:play-beep", 0);
                    toast.add({
                        severity: "error",
                        summary: _barcode,
                        detail: "Barcode is not correct. Wrong!",
                        life: 3000
                    });

                    const scanarea = "blood-tests";

                    try {
                        const { data } = await $axios.get(
                            `scan-barcode/data?barcode=${_barcode}&scanarea=${scanarea}`
                        );
                        emitter.emit("cycles-ui:redict-to-other", data);
                    } catch (e) {
                        console.log(e);
                    }

                    // const anotherPatient = '105337';
                    // const pageName = 'blood-tests';
                }
            }
            // Example: Log the value
            // console.log("Scanned barcode:", _barcode);
        };

        const toast = useToast();

        const {
            hasCreatePermission,
            hasUpdatePermission
        } = usePermissionCheck();

        const printToZebraPrinter = (data) => {
            let patient = { ...store.state.artCycle.patient };

            let base = `${ZEBRA_PRING_URL}/bt_zpl_print.php`;

            let url =
                `${base}` +
                `?first_name=${patient.firstName}` +
                `&last_name=${patient.lastName}` +
                `&chart=${patient.chart}` +
                `&dob=${patient.year}-${patient.month}-${patient.day}` +
                `&gender=${patient.sex}` +
                `&test_order_id=${data.testOrderId}` +
                `&test_orders=${data.testItems}`;

            window.open(url);
        };

        onMounted(() => {
            getBloodTestResults();

            emitter.on(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );

            emitter.on(
                "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                getBloodTestResultsWithOpenBarcode
            );

            emitter.on("cycles-ui:check-barcode", checkScannedBarcode);
        });

        onUnmounted(() => {
            emitter.off(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );

            emitter.off(
                "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                getBloodTestResultsWithOpenBarcode
            );

            emitter.off("cycles-ui:check-barcode", checkScannedBarcode);
        });

        return {
            testResults,
            selectedBloodTest,
            loading,
            showBarcode,
            showBarcodeV2,
            openPdfReport,
            add: () => {
                orderBloodTestConfirmDialog.value = true;
            },
            edit: () => {
                if (selectedBloodTest.value == null) {
                    toast.add({
                        severity: "warn",
                        summary: "Select Row",
                        detail: "Please select a row first",
                        life: 3000
                    });
                    return;
                }
                editDailogVisible.value = true;
            },
            addBlood: () => {
                if (
                    store.state.artCycle.paitent !== null &&
                    store.state.artCycle.patient.chart !== null
                ) {
                    addBloodDialogvisible.value = true;
                } else {
                    toast.add({
                        severity: "warn",
                        summary: "Select Patient",
                        detail: "Please select patient first",
                        life: 3000
                    });
                }
            },
            hasCreatePermission,
            hasUpdatePermission,
            printToZebraPrinter,
            isStyleRed
        };
    }
};
</script>
